@import "../../../sass/consts";


.goods_list_page {
  .search_type {
    padding: .3rem 0;
    background-color: $main1;

    a {
      color: white;
      padding-right: .5rem;
    }
  }

  .price_range {
    padding: .5rem 0;

    p {
      padding-right: 2rem;
    }

    .am-flexbox .am-flexbox-item {
      overflow: visible;
    }
  }

  .box {
    background-color: $bg1;
    height: 100%;
  }

  .am-tabs {
    height: auto !important;
    flex: none !important;
  }
}
